import React from 'react';
import Chair from './Chair';
import Light from './Light';
import { useState } from 'react';
import './Product.css';

export default function Product({cart,setCart}) {
 const [change,setChange]=useState("");
  return (
    <div className='product'>
      <div className='pvideo'>
        <video src={require('./video.mp4')} autoPlay muted width="90%" loop></video>
      </div>

      <div className='plist'>
       <div className='pl1'>
        <h4 onClick={()=>setChange("light")}>LIGHTS</h4>
       </div>
       <div className='pl2'>
        <h4 onClick={()=>setChange("chair")}>CHAIRS</h4>
       </div>
      </div>
     
     {change=="light"?<Light cart={cart} setCart={setCart}/>: change=="chair" ?<Chair cart={cart} setCart={setCart}/>:<Light cart={cart} setCart={setCart}/>}
    
    </div>
  )
}

