import React from 'react';
import './Profile.css';
import bg from './log2.jpg';



export default function Profile() {

  return (
    <div className='logindec'>
      <div className='loginsec'>
      <div className='logbg' style={{backgroundImage:`url(${bg})`,backgroundPosition:'center',
      backgroundRepeat:'no-repeat',backgroundSize:'cover',width:'100%'}}> 
       </div>
       <div className='logbg1'>
       <div className='logimg'>
          <img src={require("./log1.jpg")} alt="" width={"100%"} height={"100%"}/>
        </div>
        <div className='logcon' >
          <div className='container'>
          <h2 className='logtitle'>KGN 
            <br /><span>INTERIOR DESIGN</span></h2>
            <h2 className='subhead'>Welcome Back</h2>
            <button className='googlebtn'> <img src={require("./google.png")} alt="" width={"20px"}/>  Log in with Google</button>
              <h6>OR LOGIN WITH EMAIL</h6>
            <div className='logemail'>
               <h3>Email Address</h3>
              <input type="text" placeholder='Email Address' id='email' required />
            </div>
            <div className='logpass'>
              <h3>Password</h3>
              <input type="password" placeholder='Password' id='email' required maxLength={"10"} size={"10"} minLength={"8"}/>
            </div>
            <div className='logforgot'>
            <div className='logcheck'>
              <input type="checkbox" />
              <h4>Keep me logged in</h4>
            </div>
            <h5>Forgot your password?</h5>
            </div>
            <button className='logbtn' type='submit'>Log in</button>
            <h5 className='dont'>Don't have an account?<a href="#">Sign up</a></h5>
            </div>
        </div>
        
       </div>
       </div>
       
      </div>
    
  )
}
