import React from 'react';
import Item from './Data.js';
import './Homeimg.css';
export default function Homeimg() {
    const {chair}=Item;
    return (
      <div className='home-chair'>
        {
        chair.map((cards) =>(
          <div className='cards_img'>
       
          <img src={cards.image} className='ch-img' alt='' />
          </div>
        )
        
        )
      }
      </div>
    )
  }
