import React from 'react';
import { Link,Outlet } from 'react-router-dom';
import './Nav.css'
export default function Nav({cart,setCart}) {
  return (
   
      <div>
      <div className='nav'>
        <div>
          
      <h1 className='title'>KGN</h1>
    <h2 className='subtitle'>Interior Design</h2>
        </div>
        <div className='nav-dec'>
        <div>
      <Link to="/" className='nav1'>Home</Link>
      </div>
      <div>
      <Link to="/Product"  className='nav2'>Products</Link>
      </div>
      <div>
      <Link to="/Service"  className='nav3'>Service</Link>
      </div>
      <div>
      <Link to="/Contact"  className='nav4'>Contact</Link>
      </div>
      <div>
      <Link to="/Carts" className='nav4'>Cart{cart.length}</Link>
          
      </div>
      <div>
      <Link to="/Profile" className='nav4'><img src={require('./profile.png')} alt=""   width="40px"/></Link>
      </div>
      </div>
      </div>
      <Outlet></Outlet>
      </div>
     
    
  )
}
