import React from 'react';
import Item from './Data.js';
import './Chair.css';
export default function Chair({cart,setCart}) {
    function tocart1(toitems){
        if(cart.indexOf(toitems) >=0)
         return
       setCart([...cart,toitems]);
   
     }
    
    const {chair}=Item;
  return (
    <div className='chair'>
    <div className='chair-dec'>
      {
      chair.map((items) =>(
        <div className='product-chair'>
        <img src={items.image} className='chimg' alt=''  width="150px" />
        {/* <div className='wishdec1'>           
        </div> */}
        <div className='calign'>
        <h2 className='chairname'>{items.name}</h2>
        <h4 className='chairprice'>&#8377;{items.price}</h4>
        </div>
        <button onClick={()=>tocart1(items)} className='ctocart'>Add to Cart</button>
        </div>
      )
      
      )
    }
    </div>
    </div>
  )
}
