import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import './Cart.css';

export default function Cart({cart,setCart}) {
    const [ value, setValue]= useState();

    function add(cartitem,av)
    {
        const quantity=cart.indexOf(cartitem);
        cart[quantity].slot +=av;
        setCart([...cart]); 
    }

    useEffect(()=>{
        let rate=0;
        cart.map((cartitem)=>(rate += cartitem.slot*cartitem.price))
        setValue(rate);
    })

    function trash(trashbin){
        const bin=cart.filter((cartitem)=> cartitem.id !== trashbin);
        setCart(bin);
    }
    
  return (
    <div>
        
        <div>
        {
            cart.map((cartitem)=>(
                <div className='cartpage'>
                <div className='cartsec'>
                    <div className='imgname'> 
                    <div className='cartsec1'>
                    <div className='remove' onClick={()=>trash(cartitem.id)}>
                        <div className='line1'></div>
                        <div className='line2'></div>
                    </div>
                {/* <button >remove</button> */}
                    <div className='cartimg'>       
            <img src={cartitem.image}  alt=''  className='tocartimg' />
            <h2  className='tocartname'>{cartitem.name}</h2>   
            </div>        
        </div>
        </div>
        <div className='cartsec2'>
            <div className='pritotal'>
            <div className='cartpri'> 
        <h4 >&#8377;{cartitem.price}</h4> 
        </div>
        <div className='incsec'>
        <span className='tocartslot'>{cartitem.slot}</span>
        <div  className='incre'>
        <button onClick={()=>add(cartitem,1)} className='increbtn'>+</button>
        <button onClick={()=>add(cartitem,-1)} className='decrebtn'>-</button>
        </div>  
        </div>
        </div>
        </div>
         </div>
         </div>
            ))
        }
        </div>
        <div className='value'>
            <h4>Total:{value}</h4>
        </div>
    </div>
  )
}
