import React from 'react';
import './Light.css';
import Item from './Data.js';

export default function Light({cart,setCart}) {
    function tocart(toitem){
        if(cart.indexOf(toitem) >=0)
         return
       setCart([...cart,toitem]);
   
     }
    const {card}=Item;
  return (
    <div className='light'>
        <div className='products'>
      {
      card.map((items) =>(
        <div className='product-card'>
            
        <img src={items.image} className='liimg' alt='' width="150px" />
        {/* <div className='wishdec'>           
        </div> */}
        <div  className='lalign'>
        <h2 className='lightname'>{items.name}</h2>
        <h4 className='lightprice'>&#8377;{items.price}</h4>
        </div>
        <button onClick={()=>tocart(items)} className='ltocart'>Add to Cart</button>
     </div>
       
      )
      
      )
    }
    </div>
    </div>
  )
}
