import React from 'react';
import './Home.css';
import Item from './Data.js'
import Homeimg from './Homeimg';

export default function Home() {
  const {card}=Item;
  console.log(card);
  return (
    <div className='home'>
    <div className='banner'>
      <img src={require('./ban4.jpg')} alt="Home" width="100%" />
      </div>
      <div className='content'>
        <h1 className='con-txt'>FILL YOUR ROOM <br />WITH SOPHISTICATION</h1>
        <p className='con-para'>Lorem ipsum dolor sit amet consectetur adipisicing elit. 
          <br />Et incidunt obcaecati architecto atque aspernatur fuga, ducimus repr</p>
          <button className='btn'>Take a Look</button>
       </div>
       <div>
      <div className='home-img' >
     {
      card.map((itemss) =>(
        <div className='hcard-img'>
        <img src={itemss.image} className='img'   alt='' />
        </div>
      )
      )
     
    }
      </div> 
    
     <div>
      <Homeimg></Homeimg>
     </div>
     </div>
    <div className='banner1'>
    <img src={require('./ban5.jpg')} alt="Home" />
    <button className='btn1'>Check Out</button>

    </div>
    </div>
  )
}
